.SearchForm .title{font-size:20px;padding-bottom:5px;border-bottom:1px solid #eee;margin-bottom:10px;font-weight:500;text-align:center;}
.SearchForm input[type=text],.SearchForm input[type=password],.SearchForm input[type=file],.SearchForm input[type=number]{padding:6px 5px;border:none;border:1px solid #ddd;width:70%;}
